  margin-bottom: 15px;
}

figure.cc-effect {
  position: relative;
  overflow: hidden;
  text-align: center;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
  margin-bottom:0;
}

figure.cc-effect img {
  position: relative;
  display: block;
  max-width: 100%;
  opacity: 1;
  width: 100%;
  height: 300px;
  object-fit: cover;
  -webkit-transform: scale(1.06, 1.06);
  transform: scale(1.06, 1.06);
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
  transition: opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;
}

figure.cc-effect figcaption {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: #fff;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
  transition: opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;
}
 figure.cc-effect:hover figcaption::after, 
  figure.cc-effect:hover .h4,  figure.cc-effect:hover p,  figure.cc-effect:hover img {
  -webkit-transition-delay: 0.15s;
  transition-delay: 0.15s;
}
figure.cc-effect figcaption:before {
  position: absolute;
  content: '';
  opacity: 0;
  top: 10px;
  right: 10px;
  bottom: 10px;
  left: 10px;
  -webkit-transform: scale(0, 0);
  transform: scale(0, 0);
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
}

figure.cc-effect figcaption:after {
  position: absolute;
  content: '';
  opacity: 0;
  top: 10px;
  right: 10px;
  bottom: 10px;
  left: 10px;
  -webkit-transform: scale(0, 0);
  transform: scale(0, 0);
  -webkit-transform-origin: 100% 0;
  transform-origin: 100% 0;
}
 figure.cc-effect img,  figure.cc-effect figcaption::before,  figure.cc-effect figcaption::after,  figure.cc-effect p {
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
  transition: opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;
}

figure.cc-effect h4 {
  margin-top: 25%;
  -webkit-transition: -webkit-transform 0.35s;
  transition: -webkit-transform 0.35s;
  transition: transform 0.35s;
  -webkit-transform: transform 0.35s;
  opacity: 0;
  -webkit-transform: scale(1.06, 1.06);
  transform: scale(1.06, 1.06);
}

figure.cc-effect p {
  padding: 0.5em 2em;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;
  opacity: 0;
}
figure.cc-effect:hover img {
  -webkit-transition-delay: 0.15s;
  transition-delay: 0.15s;
}

figure.cc-effect:hover figcaption:before {
  background: rgba(27, 23, 23, 0.5);
}

figure.cc-effect:hover figcaption::before, figure.cc-effect:hover figcaption::after {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
}

figure.cc-effect:hover figcaption::after, figure.cc-effect:hover h4, figure.cc-effect:hover p, figure.cc-effect:hover img {
  -webkit-transition-delay: 0.15s;
  transition-delay: 0.15s;
}

figure.cc-effect:hover h4, figure.cc-effect:hover p, figure.cc-effect:hover button {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  color: #fff;
}


